import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { ResolverService } from '../service/resolver.service';

@Component({
  selector: 'app-guide',
  templateUrl: './guide.component.html',
  styleUrls: ['./guide.component.css']
})
export class GuideComponent implements OnInit {
  constructor(protected resolverService: ResolverService, private router: Router) { }
  ngOnInit() {
    const addressUrl = location.search.slice(1); // 取参数
    console.log(addressUrl);
    console.log("GuideComponent");
    const searchParams = new URLSearchParams(addressUrl);
    const param = searchParams.get('o');
    console.log(param);
    if (param != null) {
      this.resolverService.resover(location.toString(), param).subscribe(res => {
        localStorage.setItem('appData', JSON.stringify(res));
        this.router.navigate(['task'], { replaceUrl: true }).then(res => {
        }).catch( res => {
          console.log(res);
        })
      })
    } else {
      this.router.navigate(['task'], { replaceUrl: true }).then(res => {
        console.log(res);
      }).catch(res => {
        console.log(res);
      })
    }
  }
}



